<template>
  <section class="product">
    <div class="top-img product__top-img" :style="'background-image: url('+product.backgroundImg+')'">  
    </div>
    <div class="row product__info">
      <div class="col-12 col-md-4">
        <div class="img-responsive product__img">
          <img :src="product.img" :alt="product.name">
        </div>
      </div>
      <div class="col-12 col-md-8">
        <header class="product__header">
          <h2 class="product__title">{{product.name}}</h2>
          <span>{{product.shortDesc}}</span>
        </header>
        <div class="row">
          <div class="col">
            <div class="product__props">
              <h4 class="color_accent">Описание сорта</h4>
              <div class="product-prop">
                <div class="product-prop__key">Обжарка:</div>                 
                <div class="product-prop__val"><img :src="require(`../assets/level${product.roasting}.png`)" :alt="product.roasting"></div>
              </div>
              <div class="product-prop">
                <div class="product-prop__key">Крепость:</div>                  
                <div class="product-prop__val"><img :src="require(`../assets/level${product.strength}.png`)" :alt="product.strength"></div>
              </div>
              <div class="product-prop">
                <div class="product-prop__key">Горчинка:</div>                  
                <div class="product-prop__val"><img :src="require(`../assets/level${product.bitterness}.png`)" :alt="product.bitterness"></div>
              </div>
              <div class="product-prop">
                <div class="product-prop__key">Кислинка:</div>                  
                <div class="product-prop__val"><img :src="require(`../assets/level${product.sour}.png`)" :alt="product.sour"></div>
              </div>
            </div>
          </div>
          <div class="col">
            <div class="product__props">
              <h4 class="color_accent">Стоимость</h4>
              <div class="product-prop">
                <span class="product-prop__key">250 г.:</span>
                <strong class="product-prop__val product-prop__val_price">{{product.price250}} руб.</strong>
              </div>
              <div class="product-prop">
                <span class="product-prop__key">1 кг.:</span>
                <strong class="product-prop__val product-prop__val_price">{{product.price1000}} руб.</strong>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <article class="product__desc html-content" v-html="product.description">
    </article>
    <a class="btn" @click="$router.go(-1)"><font-awesome-icon icon="arrow-left" class="btn__icon"/> Назад</a>
  </section>
  
  <!-- <button @click="returnToList()">Назад</button> -->
</template>
<script>
  import productService from '../productService.js'

  export default {
    components:{
    
    },
    data(){
      return{
        product:{}
      }
    },
    methods:{
    },
    created(){
      productService.$on("viewDetails", (selectedproduct) => {
        this.product=selectedproduct;
      })
    },
    mounted(){
      productService.viewDetails(this.$route.params.id);
    },
    computed:{

    },
    watch:{
      '$route.params.id'(id){ //???????????
        productService.viewDetails(id);
      }
    }
  }
</script>
<style lang="scss">
  .product{
    &__background-img{
      
    }
    &__header{
      text-transform: uppercase;
      text-align: center;
      margin-bottom: 20px;
    }
    &__title{
      font-family: 'rockwell', sans-serif;
      margin-bottom: 0;
    }
    &__img{
      border-radius: 50%;
      overflow:hidden;
      background-color: $white;
      max-width: 300px;
      margin: 0 auto;
    }
    &__top-img{
      min-height: 100px;
    }
    &__info{
      margin-top: -60px;
    }
    &__desc{
      margin-top: 20px;
      margin-bottom: 60px;
    }
    &__props{ 
      margin-bottom:20px;
    }
  }
  .product-prop{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
   
    
    &__val{
      &_price{
        font-size: 1.3rem;
        color:$accent;
      }
    }
  }

  @include media-breakpoint-up(lg) {
    
  }

  @include media-breakpoint-up(md) {
    .product{
      &__header{
        color:$white;
        text-align: left;
      }
      &__desc{
        margin-top: 40px;
      }
      &__top-img{
        min-height: 180px;
      }
      &__props{
        max-width: 200px;
        margin: 0;
      }
    }

  }

  @include media-breakpoint-up(sm) {

  }

  @include media-breakpoint-up(xs) {

  }
</style>